const getColorForStatus = (status) => {
  switch (status) {
    case "Pending":
      return "bg-yellow-400";
    case "Confirmed":
      return "bg-green-400";
    case "Shipping":
      return "bg-blue-300";
    case "Out For delivery":
      return "bg-cyan-500";
    case "Delivered":
      return "bg-lime-500";
    case "Returned":
      return "bg-orange-400";
    case "Canceled":
      return "bg-red-500";
    case "Completed":
      return "bg-green-500";
    default:
      return "bg-gray-200";
  }
};

